.filter-panel {
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 300px;
  z-index: 10000;
  transition: .25s left ease-in-out;
  background-color: #f0f1f2;

  display: grid;
  grid-template-rows: 60px 40px 1fr 120px;

  & .filter-title {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 15px;
    background-color: white;
    border-bottom: 3px solid #e6e6e6;
    font-weight: 700;
    font-size: 1.2rem;

    & svg {
      font-size: 2rem;
      cursor: pointer;
      transition: .15s color ease-in-out;

      &:hover {
        color: #888;
      }
    }
  }

  & .filter-actions {
    display: grid;
    grid-template-columns: 60px 1fr 60px;
    column-gap: 2px;
    color: white;
    border-bottom: 3px solid #e6e6e6;
    background-color: #f67e35;

    & .filter-btn, div {
      cursor: pointer;
      background-color: #f16136;
      width: 100%;
      height: 100%;
      padding: 8px;
      transition: .25s background-color ease-in-out;

      &:hover {
        background-color: #f17e5b;
      }
    }

    & div {
      display: flex;
      align-items: center;
      justify-content: center;
      
      & svg {
        margin-right: 10px;
      }
    }
  }

  & .filter-items {
    display: grid;
    grid-template-rows: repeat(auto-fill, 40px);
    row-gap: 15px;
    padding: 20px 10px;
  }

  & img {
    align-self: center;
    justify-self: center;
    height: 40px;
    opacity: .4;
  }
}

.filter-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: rgba(#000, .4);
}

.lui-dialog-container, .lui-popover-container, .senseloader-text {
  z-index: 999999;
}

.title-wrapper {
  background-color: white;
}

.qv-collapsed-listbox .title-wrapper .title {
  text-align: center !important;
  color: #f16136;
}