$padding: 16px;

.App {
  height: 100vh;
  width: 100vw;
  font-family: 'Nunito', sans-serif;
  display: flex;
  flex-direction: column;
}

.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content {
  height: 100%;
  width: 100%;
  max-width: calc(1280px + 32px);
  padding: 0 16px;
}

