.menu {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  position: relative;
  padding: 0 25px 25px 25px;
  border-bottom: 3px solid #e6e6e6;

  & .menu-btn {
    width: 170px;
    text-align: center;
    border: 1px solid rgba(#fc9c34, .5);
    background-color: #f16136;
    text-decoration: none;
    padding: 10px;
    color: white;
    font-weight: bold;
    transition: .25s background-color ease-in-out;

    &:hover {
      background-color: rgba(#f16136, .8);
    }
  }

  & .menu-btn-right {
    border-radius: 0 12px 12px 0;
  }

  & .menu-btn-left {
    border-radius: 12px 0 0 12px;
  }

  & .btn-filter {
    position: absolute;
    left: 0;
    font-size: 1.7em;
    display: flex;
    color: #f16136;
    justify-content: center;
    cursor: pointer;
    transition: .15s color ease-in-out;

    &:hover {
      color: rgba(#f16136, .8);
    }
  }

  & span {
    position: absolute;
    right: 0;
    color: #aaa;
    text-align: right;

    @media screen and (max-width: 700px) {
      display: none;
    }
  }
}

@media screen and (max-width: 700px) {
  .btn-filter-mobile {
    position: fixed;
    bottom: 30px;
    right: 30px;
    background: #f16136;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    border-radius: 8px;
    z-index: 15;
  }
}