.custom-kpi {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  & img {
    height: 60px;
  }
}

.custom-kpi-content {
  display: inherit;
  flex-direction: column-reverse;
  padding-left: 20px;
  // line-height: 28px;

  & .custom-kpi-title {
    font-size: 1rem;
    color: #999;
  }

  & .custom-kpi-value {
    font-size: 2.2rem;
    font-weight: bold;
  }
}

