.nav {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 50px;
  height: 50px;
  background-color: white;

  & img {
    height: 25px;
    width: 230px;
    object-fit: contain;
  }

  & span {
    font-family: 'Bebas Neue', cursive;
    font-size: 1.5rem;
  }
}

.nav-socials {
  width: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;

  & a {
    background-color: #323232;
    color: white;
    width: 30px;
    height: 30px;

    padding: 8px;
    margin: 6px;
    border-radius: 50%;
    transition: .15s background-color ease-in-out;

    &:hover {
      background-color: rgba($color: #323232, $alpha: .5);
    }
  }
}

.logo-mobile {
  display: none;
}

// -----------------------------------------
@media screen and (max-width: 700px) {
  .nav {
    position: relative;
    justify-content: center;
    padding: 20px 0;
    
    & img {
      display: none;
      width: auto;
    }

    & .logo-mobile {
      display: block;
      position: absolute;
      left: 20px;
      top: 25%;
    }
  }
  .nav-socials {
    display: none;
  }
}